.container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 30px;
}

.addForm {
    padding: 20px;
}

.todolist {
    width: 300px;
}

@media screen and (max-width: 678px) {
    .container {
        flex-direction: row;
        justify-content: center;
    }
}
